.btn-margin { margin: 0px 5px }
.Div, .Header {
-webkit-user-select: none;
   -moz-user-select: none;
    -ms-user-select: none;
        user-select: none;
}
.About {
    -webkit-transition: 0.5s;
    transition: 0.5s; 
    display: block; 
    width: 30%; 
    max-width: 120px; 
    margin: 20px auto; 
    z-index: 1;
    overflow: hidden;
}
#guide.Panel {
    overflow:hidden;
    }
.gallery_custom { 
    height: 300px; 
    } 
@media(max-height: 550px) { 
    .gallery_custom { 
    height: 280px; 
    } 
    .Gallery_Div { 
    font-size: 11pt; 
    } 
} 
@media(max-height: 500px) { 
    .Gallery_Div { 
    height: 220px; 
    } 
    .Gallery_Div { 
    font-size: 10pt; 
    } 
} 
@media(max-height: 450px) { 
    .gallery_custom { 
    height: 160px; 
    } 
    .Gallery_Div { 
    font-size: 8pt; 
    } 
}
#error.Panel .tabbar_guide {
    overflow: hidden;
}

